import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TweenMax } from 'gsap';
import { isEqual } from 'lodash';

export default class InputField extends Component {
  constructor(props) {
    super(props);
    this.DEFAULT_STATE = {
      value: "",
      error: "",
      valid: !this.props.isRequired,
      showError: false
    }
    this.state = this.DEFAULT_STATE;
    this.acceptString = '';
    this.props.accept.forEach(extension => {
      switch (extension.toLowerCase()) {
        case 'pdf':
          this.acceptString += 'application/pdf,'
        case 'doc':
          this.acceptString += 'application/msword,'
        case 'docx':
          this.acceptString += 'application/vnd.openxmlformats-officedocument.wordprocessingml.document,'
      }
    });
    this.FILE_INPUT_PLACEHOLDER = `Upload File (${this.props.accept.join(', ')})`;
  }

  componentWillMount() {
    if (this.props.type === 'file') this.setState({ secondaryTitle: this.FILE_INPUT_PLACEHOLDER });
  }

  componentDidUpdate() {
    if (this.props.showError === true && this.state.showError === false) {
      this.validate(this.Input);
    }
    // this.adjustErrorHeight();
  }

  componentWillUnmount() {
    this.state = {};
  }

  handleChange = (e) => {
    this.validate(e.target);
    if (this.props.type === 'file') {
      this.setState({ secondaryTitle: (e.target.value || this.FILE_INPUT_PLACEHOLDER).replace(/^.*[\\\/]/, ''), files: e.target.files });
    };
    this.props.onChange && this.props.onChange(e, this);
  }

  adjustErrorHeight = () => {
    if (this.state.showError && this.state.error !== "") {
      TweenMax.to(this.container, 0.25, {marginBottom: (this.error.getBoundingClientRect().height - 4)})
    } else {
      TweenMax.to(this.container, 0.25, {marginBottom: 0})
    }
  }

  validate = (target) => {
    if (!target) return;
    if (this.props.isRequired) {
      var valid = true, errMsg = "";
      if (target.value.length === 0) {
        valid = false;
        errMsg = "Required";
      } else {
        if (!this.props.skipValidation) {
          switch (this.props.type) {
            case "password":
              if (this.props.compareTo) {
                if (target.value !== this.props.compareTo) {
                  valid = false;
                  errMsg = "Passwords do not match. Please try again.";
                }
              } else if (target.value.length < 6 || target.value.length > 32) {
                valid = false;
                errMsg = "Password length must be between 6 and 32";
              }
              break;
            case "email":
              if (!target.value.match(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) {
                valid = false;
                errMsg = "Please enter a valid email address";
              }
              break;
            case "phone":
              if (!target.value.match(/^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/)) {
                valid = false;
                errMsg = "Please enter a valid phone number (eg. 123-456-7890)";
              }
              break;
            case "file":
              let regexStr = '(';
              this.props.accept.forEach((extension, index) => {
                regexStr += extension;
                if (index < this.props.accept.length - 1) regexStr += '|'
              })
              regexStr += ')';
              if (!target.value.replace(/^.+[\.]/, '').match(new RegExp(regexStr, 'i'))) {
                valid = false;
                errMsg = "Please choose an appropriate file to upload";
              }
              break;
            case "postalcode":
              if (!target.value.match(/([ABCEGHJKLMNPRSTVXY][0-9])[ABCEGHJKLMNPRSTVWXYZ][- ]?[0-9][ABCEGHJKLMNPRSTVWXYZ][0-9]/i)) {
                valid = false;
                errMsg = "Please enter a valid postal code";
              }
              break;
            default:
          }
        }
      }
      const nextState = { value: target.value, error: errMsg, valid: valid, showError: true };
      if (!isEqual(nextState, this.state)) this.setState(nextState);
    } else {
      // this.setState({value: target.value, error: "", valid: true, showError: true});
      this.setState({ value: target.value, error: "", valid: true, showError: false });
    }
  }

  toggleFocus = (on) => {
    if (on) {
      if (this.container.className.indexOf("focused") == -1)
        this.container.className += " focused";
    } else {
      this.container.className = this.container.className.replace(" focused", "");
    }
  }

  reset = () => {
    this.setState(this.DEFAULT_STATE);
  }

  focusInput = () => {
    if (this.label) this.label.click();
  }

  render() {
    const { id, label, type, minLength, maxLength } = this.props;
    const { showError, error, secondaryTitle, value } = this.state;
    return (
      <div className={`FormInput TextField ${showError && error ? 'error' : ''}`} ref={el => this.container = el}>
        <label htmlFor={ id } ref={el => this.label = el}>{ label }</label>
        { secondaryTitle &&
          <p
            className="secondaryTitle"
            role="button"
            onClick={this.focusInput}
            onKeyDown={this.focusInput}
          >
            { secondaryTitle }
          </p>
        }
        <input
          value={ value }
          id={ id }
          ref={el => this.Input = el}
          accept={ this.acceptString }
          maxLength={ maxLength || '' }
          minLength={ minLength || '' }
          onChange={ this.handleChange }
          onFocus={ this.toggleFocus.bind(this, true) }
          onBlur={ this.toggleFocus.bind(this, false) }
          type={ type } />
        {/* { showError && error !== "" && <span className="error" ref={el => this.error = el}>{error}</span> } */}
      </div>
    );
  }
}

InputField.propTypes = {
  accept: PropTypes.array,
  id: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string
  ]).isRequired,
  isRequired: PropTypes.bool,
  label: PropTypes.string,
  maxLength: PropTypes.number,
  minLength: PropTypes.number,
  type: PropTypes.string.isRequired
}

InputField.defaultProps = {
  accept: [],
  isRequired: false,
  type: 'text'
}