import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import TweenMax from 'gsap';
import Link from 'next/link'
import settings from '../../util/settings';
import data from '../../static/json/data.json';
import makePath from '../../util/make-path';
import Toggle from 'react-toggle';

export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: true,
      responsive: false
    }
    this.MIN_SCREEN_WIDTH = 768;
    this.links = []
    this.menuHeight = 0;
    this.animOn = false;
    this.pages = ["home", "about", "services", "testimonials", "contact"]

    // Add sublinks for mobile view
    this.sublinks = {
      'services': [],
      'contact': [
        { title: 'Contact Us',
          url: '/contact'
        },
        { title: 'Careers',
          url: '/careers'
        }
      ]
    };
    data.services.map(service => {
      this.sublinks.services.push({
        title: service.name,
        url: `/services/${makePath(service.name)}`
      })
    })

    this.ASSET_PATH = settings.ASSET_PATH + "images/"
  }

  componentDidMount() {
    document.addEventListener("scroll", this.handleScroll)
    window.addEventListener("resize", this.checkResponsive)
    this.checkResponsive();
    setTimeout(this.handleScroll, 200)

    document.body.className += ' cta-button';

    if (!this.state.responsive) {
      TweenMax.fromTo(this.links, 1, {x: -10, y: -5}, {x: 0, y: 0, ease: Expo.easeOut, delay: 1})
    }
  }

  componentDidUpdate() {
    this.checkResponsive();
    if (this.state.collapsed) this.menuHeight = this.Menu.getBoundingClientRect().height;
  }

  checkResponsive = () => {
    document.body.style.overflow = "auto"
    if (!settings.isServer()) {
      var responsive = window.innerWidth < this.MIN_SCREEN_WIDTH;
      if (responsive != this.state.responsive) {
        this.setState({ responsive: responsive, collapsed: true })
      }
    }
  }

  toggleMenu = (force, callback) => {
    if (this.animOn) return;
    var tl = new TimelineMax({paused: true, onComplete: ()=>{
              if (!this.state.collapsed) {
                document.body.style.overflow = "auto"
              }
              this.animOn = false;
              this.setState({collapsed: !this.state.collapsed})
            }}),
        chldrn = this.MenuBtn ? this.MenuBtn.children : [],
        time = 0.25;

    this.animOn = true;
    if (this.state.collapsed || (force && force == "open")) {
      document.body.style.overflow = "hidden"
      tl.set(this.collapsedLogo, {zIndex: 0});
      tl.set(this.linkContainer, {autoAlpha: 1, display: "block"});
      tl.to([chldrn[0], chldrn[1]], time, {y: 25, ease: Expo.easeOut})
      tl.to([chldrn[4], chldrn[5]], time, {y: -25, ease: Expo.easeOut, delay: -time})
      tl.set([chldrn[2], chldrn[3]], {autoAlpha: 0, onComplete: ()=> {
        if (!this.props.solidStyle) this.addClass(this.Menu, "solidStyle")
      }})
      tl.to(this.Menu, time, {height: "100vh", ease: Expo.easeOut})
      tl.to([chldrn[0], chldrn[1]], time, {rotationZ: "-45deg", transformOrigin: "center center", ease: Expo.easeOut, delay: -time})
      tl.to([chldrn[4], chldrn[5]], time, {rotationZ: "-135deg", transformOrigin: "center center", ease: Expo.easeOut, delay: -time*0.8})
      tl.to([chldrn[0], chldrn[2], chldrn[4]], time, {stroke: 'transparent', ease: Expo.easeOut, delay: -time})
      tl.to([chldrn[1], chldrn[3], chldrn[5]], time, {stroke: settings.colors.primary, ease: Expo.easeOut, delay: -time})
      tl.staggerFromTo(this.linkContainer.children, time, {autoAlpha: 0, y: -20}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 0.1)
    } else if (!this.state.collapsed || (force && force == "close")) {
      tl.to([chldrn[4], chldrn[5]], time, {rotationZ: "0deg", transformOrigin: "center center", ease: Expo.easeIn})
      tl.to([chldrn[0], chldrn[1]], time, {rotationZ: "0deg", transformOrigin: "center center", ease: Expo.easeIn, delay: -time*0.95})
      tl.set([chldrn[2], chldrn[3]], {autoAlpha: 1})
      tl.to(this.Menu, time, {height: this.menuHeight || 60, ease: Expo.easeOut, onComplete: ()=> {
        if (!this.props.solidStyle) this.removeClass(this.Menu, "solidStyle")
      }})
      tl.staggerTo(this.linkContainer.children, time, {autoAlpha: 0, y: -10, ease: Expo.easeOut}, 0.1)
      tl.to([chldrn[0], chldrn[2], chldrn[4]], time, {stroke: settings.colors.primary, ease: Expo.easeOut, delay: -time})
      tl.to([chldrn[1], chldrn[3], chldrn[5]], time, {stroke: settings.colors.primaryLight, ease: Expo.easeOut, delay: -time})
      tl.to([chldrn[4], chldrn[5], chldrn[0], chldrn[1]], time, {y: 0, ease: Expo.easeOut, delay: -time, onComplete: ()=> {
        tl.set(this.linkContainer, {autoAlpha: 0, display: "none"});
        tl.set(this.collapsedLogo, {zIndex: 1});
      }})
    }
    tl.play();
    if (typeof callback == "function") setTimeout(callback, (tl._totalDuration || time) * 1000);
  }

  handleScroll = () => {
    // detect position on scroll and style accordingly
    if (!settings.isServer() && !this.props.solidStyle && this.state.collapsed) {
      window.pageYOffset < 40 ? this.removeClass(this.Menu, "solidStyle") : this.addClass(this.Menu, "solidStyle")
    }
  }

  getLink = (page, index) => {
    if (page) {
      const title = page.replace(/\-/g, ' ');
      const link = `/${page === 'home' ? '' : page}`;
      return (
        <Fragment key={index}>
          <Link href={link} ref={el => this.links[page] = el}>
            <a className={"mainlink col-12 " + (this.state.responsive ? "responsive" : "")}>
              { title }
            </a>
          </Link>
          { this.getSubLinks(this.sublinks[page]) }
        </Fragment>
      )
    }
  }

  getSubLinks = (sublinks = []) => {
    return (
      <div className='sublinks d-block d-md-none'>
        { sublinks.map((link, index) => 
            <Link href={link.url} key={index}>
              <a className="sublink">{ link.title }</a>
            </Link>
          )
        }
      </div>
    )
  }

  getMenuClasses = () => {
    var classes = [];
    if (this.state.responsive) classes.push("responsive")
    if (this.props.solidStyle) classes.push("solidStyle")
    return (" " + classes.join(" "))
  }

  addClass = (obj, cls) => {
    if (obj && cls) {
      if (!obj.className.match(cls)) obj.className += " " + cls
    }
  }

  removeClass = (obj, cls) => {
    if (obj && cls) {
      if (obj.className.match(cls)) obj.className = obj.className.replace(" " + cls, "")
    }
  }

  toggleCTA = e => {
    document.body.className = document.body.className.replace(/\s{2,}/, '');
    document.body.className = document.body.className.replace(/cta-button/ig, '');
    document.body.className = document.body.className.replace(/cta-text/ig, '');
    document.body.className = [e.target.checked ? 'cta-button' : 'cta-text', ...document.body.className.split(' ')].join(' ');
  }

  render() {
    this.links = []
    return (
      <nav id="Menu" className={"navbar sticky-top" + this.getMenuClasses()} ref={el => this.Menu = el}>
        <div  className={"container" + (this.state.responsive ? " responsive" : "")}>
            <a href='/' id="collapsedLogo-container">
              <img
                id="collapsedLogo"
                alt="Brand Logo"
                className="menu-logo"
                src={this.ASSET_PATH + "brand-landing.png"}
                ref={el => this.collapsedLogo = el} />
            </a>
            { !settings.isServer() &&
                ( this.state.responsive ? (
                  <div id="subcontainer"
                      className={"row" + (this.state.responsive ? " responsive" : "")}>
                    <div className="col-12" style={{padding: 0, minHeight: 40}}>
                      <div style={{ marginRight: 50, float: 'right' }}>
                        <Toggle defaultChecked={true} onChange={this.toggleCTA} />
                      </div>
                      <svg viewBox="0 0 70 56" id="hamburger-menu" ref={el => this.MenuBtn = el} onClick={this.toggleMenu}>
                        <line x1={1} y1={7} x2={69} y2={7} className="shadow"/>
                        <line x1={1} y1={5} x2={69} y2={5}/>
                        <line x1={1} y1={32} x2={69} y2={32} className="shadow"/>
                        <line x1={1} y1={30} x2={69} y2={30}/>
                        <line x1={1} y1={57} x2={69} y2={57} className="shadow"/>
                        <line x1={1} y1={55} x2={69} y2={55}/>
                      </svg>
                    </div>
                    <div className="col-12" ref={el => this.linkContainer = el} style={{display: "none"}}>
                      { this.pages.map((item, index) => (
                          <div className="row" key={index}>
                            { this.getLink(item, index) }
                          </div>
                      ))}
                    </div>
                  </div>
                ) : (
                  <Fragment>
                    <div style={{ marginLeft: 100 }}>
                      <Toggle defaultChecked={true} onChange={this.toggleCTA} />
                    </div>
                    <div id="subcontainer"
                        className={"row" + (this.state.responsive ? " responsive" : "")}>
                      <div className="col-12">
                        <span className="d-block d-md-none">{ this.getLink(this.pages[0], 0) }</span>
                        { this.pages.slice(1).map((item, index) => this.getLink(item, index)) }
                      </div>
                    </div>
                  </Fragment>
                )
              )
            }
        </div>
      </nav>
    );
  }
}

Menu.propTypes = {
  device: PropTypes.string
};

Menu.defaultProps = {
  device: "desktop"
};
