import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class SelectList extends Component {
  constructor(props) {
    super(props);
    this.DEFAULT_STATE = {
      value: this.props.initialValue || this.props.options[0]
    }
    this.state = this.DEFAULT_STATE;
  }

  handleChange = (e) => {
    if (this.props.onChange) {
      this.props.onChange(e.target.value);
    }
    this.setState({value: e.target.value});
  }

  reset = () => {
    this.setState(this.DEFAULT_STATE);
  }

  render() {
    const { id, label, options } = this.props;
    const { showError, error, value } = this.state;
    return (
      <div className={`FormInput SelectList ${showError && error ? 'error' : ''}`} ref={el => this.container = el}>
        <label htmlFor={ id }>{ label }</label>
        <select
          id={ id }
          value={value}
          onChange={this.handleChange}>
          {
            options.map( (item, index) => 
              <option value={item} key={index}>{item}</option>
            )
          }
        </select>
      </div>
    );
  }
}

SelectList.propTypes = {
  id: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string
  ]).isRequired,
  initialValue: PropTypes.any,
  label: PropTypes.string,
  options: PropTypes.array
}

SelectList.defaultProps = {
  initialValue: '',
  label: '',
  options: []
}