import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TweenMax } from 'gsap';
import { isEqual } from 'lodash';

export default class TextArea extends Component {
  constructor(props) {
    super(props);
    this.DEFAULT_STATE = {
      value: "",
      error: "",
      valid: !this.props.isRequired,
      showError: false
    };
    this.state = this.DEFAULT_STATE;
  }

  componentDidUpdate() {
    if (this.props.showError === true && this.state.showError === false) {
      this.validate(this.Input);
    }
    // this.adjustErrorHeight();
  }

  handleChange = (e) => {
    this.validate(e.target);
    this.props.onChange(e);
  }

  validate = (target) => {
    if (!target) return;
    if (this.props.isRequired && this.props.isRequired !== false) {
      var valid = true, errMsg = "";
      if (target.value.length === 0) {
        valid = false;
        errMsg = "Required";
      }
      this.setState();
      const nextState = { value: target.value, error: errMsg, valid: valid, showError: true };
      // const nextState = { value: target.value, error: errMsg, valid: valid, showError: false };
      if (!isEqual(nextState, this.state)) this.setState(nextState);
    } else {
      this.setState({ value: target.value, error: "", valid: true, showError: false });
      // this.setState({value: target.value, error: "", valid: true, showError: true});
    }
  }

  adjustErrorHeight = () => {
    if (this.state.showError && this.state.error !== "") {
      TweenMax.to(this.container, 0.25, {marginBottom: (this.error.getBoundingClientRect().height - 9)})
    } else {
      TweenMax.to(this.container, 0.25, {marginBottom: 0})
    }
  }

  toggleFocus = (on) => {
    if (on) {
      if (this.container.className.indexOf("focused") == -1)
        this.container.className += " focused";
    } else {
      this.container.className = this.container.className.replace(" focused", "");
    }
  }

  reset = () => {
    this.setState(this.DEFAULT_STATE);
  }

  render() {
    const { id, label, placeholder, maxLength } = this.props;
    const { showError, error, value } = this.state;
    return (
      <div className={`FormInput TextArea ${showError && error ? 'error' : ''}`} ref={el => this.container = el}>
        <label htmlFor={ id }>
          { label }
          { maxLength - value.length <= (maxLength/10) && ` -  ${maxLength - value.length} characters left` }
        </label>
        <textarea value={ value } id={ id } onChange={ this.handleChange }
          placeholder={ placeholder }
          ref={el => this.Input = el}
          onFocus={this.toggleFocus.bind(this, true)}
          onBlur={this.toggleFocus.bind(this, false)}
          maxLength={ maxLength } />
        {/* { showError && error && (<span className="error" ref={el => this.error = el}>{ error }</span>) } */}
      </div>
    );
  }
}

TextArea.propTypes = {
  id: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string
  ]).isRequired,
  isRequired: PropTypes.bool,
  label: PropTypes.string,
  maxLength: PropTypes.number,
  placeholder: PropTypes.string,
}

TextArea.defaultProps = {
  isRequired: false,
  label: '',
  maxLength: 500,
  placeholder: ''
}