'use strict';
import React, { Component, Fragment } from 'react';
import { TweenMax, TimelineMax } from 'gsap';
import Recaptcha from 'react-google-recaptcha';
import settings from '../../util/settings';
import Social from '../../components/Social/Social';
import { InputField, TextArea, SelectList } from '../../components/Form/index';
import fetch from 'isomorphic-fetch';
import ReactGA from 'react-ga';
import { cloneDeep } from 'lodash';

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      validCaptcha: settings.isDev,
      showError: false,
      email: {
        submitted: false,
        sent: false,
        failed: false,
        formError: ''
      },
      analyticsSubmitted: {
        scroll: false,
        form: false
      }
    }
    this.AnimationElements = []
    this.InputFields = []
    this.SelectLists = []
  }
  componentDidMount() {
    TweenMax.to(this.container, 0.5, {autoAlpha: 1});
    TweenMax.set(this.AnimationElements, {autoAlpha: 0});
    this.animateIn();

    window.addEventListener('scroll', this.handleScroll);
    this.handleScroll()
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    TweenMax.set(this.container, {autoAlpha: 0});
  }
  handleInputChange = (e, item)=>{

  }
  handleScroll = (e) => {
    if (this.container.getBoundingClientRect().top < window.innerHeight && !this.state.analyticsSubmitted.scroll) {
      ReactGA.modalview('contact');
      this.setAnalyticSubmission('scroll')
    }
    if (this.props.isResponsive) {
      if (this.container.getBoundingClientRect().top <= window.innerHeight/2) {
        this.animateIn();
        window.removeEventListener('scroll', this.handleScroll);
      }
    } else {
      if (this.container.getBoundingClientRect().top < window.innerHeight) {
        this.animateIn();
        window.removeEventListener('scroll', this.handleScroll);
      }
    }
  }
  setAnalyticSubmission = (name) => {
    if (name) {
      this.setState({analyticsSubmitted: {...this.state.analyticsSubmitted, [name]: true}})
    }
  }
  animateIn = () => {
    var tl = new TimelineMax({paused: true});
    tl.staggerFromTo(this.AnimationElements, 2, {autoAlpha: 0, y: 50}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 0.25);
    tl.play();
  }
  handleReCaptcha = (value) => {
    this.setState({
      validCaptcha: value
    })
  }
  handleSubmit = (e) => {
    e.preventDefault();

    let emailState = cloneDeep(this.state.email);
    emailState.formError = '';
    
    let valid = true;
    for (var i = 0; i < this.InputFields.length; i++) {
      if (!this.InputFields[i].state.valid) {
        emailState.formError = 'Please fill out all form fields';
        valid = false;
        break;
      }
    }
    if (!this.state.validCaptcha) {
      valid = false;
      emailState.formError = 'Please complete the reCAPTCHA';
    }
    if (valid) {
      emailState.submitted = true;
      this.setState({ email: emailState });
      fetch(settings.server + 'mail.php', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          "Content-type": "application/x-www-form-urlencoded"
        },
        body: JSON.stringify(this.getFormContent())
      }).then((response) => {
        if (!response.ok) {
          emailState.failed = true;
          this.setState({ email: emailState });
          throw new Error('Something went wrong');
        } else {
          this.resetForm();
          emailState.sent = true;
          this.setState({ email: emailState, showError: false });
        }
        ReactGA.event({
          category: 'Contact',
          action: 'Submit',
          label: response.ok ? 'Succeeded' : 'Failed'
        });
        return response.json();
      }).then((data)=>{
        if (settings.isDev) {
          // console.log(data)
          document.open();
          document.write(data);
        }
      }).catch(msg => {
        emailState.failed = true;
        this.setState({ email: emailState });
      })
    } else {
      this.setState({ email: emailState, showError: true });
    }
  }

  getFormContent = () => {
    var obj = {};
    for (var i = 0; i < this.InputFields.length; i++) {
      obj[this.InputFields[i].props.id] = this.InputFields[i].state.value
    }
    for (var i = 0; i < this.SelectLists.length; i++) {
      obj[this.SelectLists[i].props.id] = this.SelectLists[i].state.value
    }
    return obj;
  }

  resetForm = () => {
    this.InputFields.forEach( field => {
      field.reset();
    });
    this.SelectLists.forEach( field => {
      field.reset();
    });
  }

  render() {
    this.AnimationElements = []
    this.InputFields = []
    this.SelectLists = []
    const { title, description } = this.props;
    return <div
      className="section-contact"
      ref={el => this.container = el}
    >
      <div className="ContentContainer">
        <h1 className="dark">{ title || 'Contact Us' }</h1>
        { description &&
          <p className="dark mini" style={{ maxWidth: 430, marginBottom: '2.5rem' }}>
            { description }
          </p>
        }
        <div className="row">
          <div className="col-md-7" style={{ maxWidth: 600 }}>
            <form onSubmit={this.handleSubmit}>
              {/* InputFields */}
              <div className="row" ref={el => this.AnimationElements[0] = el}>
                <div className="col-sm-6">
                  <InputField label="First Name" id="fname"
                              showError={this.state.showError} ref={el => this.InputFields[0] = el}
                              onChange={this.handleInputChange} isRequired />
                </div>
                <div className="col-sm-6">
                  <InputField label="Last Name" id="lname"
                              showError={this.state.showError} ref={el => this.InputFields[1] = el}
                              onChange={this.handleInputChange} isRequired />
                </div>
              </div>
              <div className="row" ref={el => this.AnimationElements[1] = el}>
                <div className="col-sm-6">
                  <InputField label="Email" id="email" isRequired type="email"
                              showError={this.state.showError} ref={el => this.InputFields[2] = el} />
                </div>
                <div className="col-sm-6">
                  <InputField label="Phone" id="phone" isRequired type="phone"
                              showError={this.state.showError} ref={el => this.InputFields[3] = el} />
                </div>
              </div>
              <div className="row" ref={el => this.AnimationElements[2] = el}>
                <div className="col-sm-6">
                  <SelectList
                    ref={el => this.SelectLists[0] = el}
                    id="preferredcontact"
                    label="Preferred Contact"
                    options={[ 'Email', 'Phone' ]} />
                </div>
                <div className="col-sm-6">
                  <InputField label="Postal Code" id="postalcode" isRequired type="postalcode" minLength={6} maxLength={7}
                              showError={this.state.showError} ref={el => this.InputFields[4] = el} />
                </div>
              </div>
              {/* TextArea */}
              <div className="row" ref={el => this.AnimationElements[3] = el}>
                <div className="col-12">
                  <TextArea label="Message" id="message" placeholder="How can we help you? When is the best time to contact you?"
                            isRequired showError={this.state.showError}
                            onChange={this.handleInputChange} ref={el => this.InputFields[5] = el}/>
                </div>
              </div>
              <div ref={el => this.AnimationElements[4] = el} style={{ marginBottom: 10 }}>
                <Recaptcha
                  key="captcha"
                  className="recaptcha"
                  onChange={this.handleReCaptcha}
                  sitekey={settings.googleReCaptchaKey}
                />
              </div>
              <div ref={el => this.AnimationElements[5] = el}>
                <a role='button' className='cta dark' onClick={ this.handleSubmit }>Send Message</a>
              </div>
              <div className="delivery-info" ref={el => this.AnimationElements[6] = el}>
              { this.state.email.submitted ? (
                  <Fragment>
                    { this.state.email.sent &&
                      <Fragment>
                        <p style={{ fontWeight: 'bold' }}>Your message has been sent!</p>
                        <p>A member of our team will be in touch with you as soon as possible.</p>
                      </Fragment>  
                    }
                    { this.state.email.failed &&
                      <p>Sorry, there was an error sending your email. Please try again later.</p>
                    }
                    { !this.state.email.sent && !this.state.email.failed &&
                      <p>Sending email...</p>
                    }
                  </Fragment>
                ) : (
                  <p>{ this.state.email.formError }</p>
                )
              }
              </div>
            </form>
          </div>
          <div className="offset-md-1 col-md-4 contact-details d-none d-md-block">
            <h6>Contact Info</h6>
            <p>Phone: <a href='tel:+18002088480'>1-800-208-8480</a></p>
            <p>Fax: 1-844-299-8480</p>
            <p>E-mail: <a href='mailto:info@hospall.com'>info@hospall.com</a></p>
            <h6 className="offset">Connect With Us</h6>
            <Social />
          </div>
        </div>
      </div>
    </div>;
  }
};

Contact.defaultProps = {
  windowWidth: 960,
  windowHeight: 570
};

export default Contact;
