'use strict';
import React, { Fragment } from 'react';
import { TweenMax, TimelineMax, EasePack} from 'gsap';
import Recaptcha from 'react-google-recaptcha';
import settings from '../../util/settings';
import ReactGA from 'react-ga';
import ChevronCircleSVG from '../../static/svg/chevon-circle-right.svg'
import data from '../../static/json/data.json';

class Testimonials extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      analyticsSubmitted: {
        scroll: false,
        form: false
      },
      current: 0
    }
    this.AnimationElements = []
    this.animating = false;
    this.ASSET_PATH = settings.ASSET_PATH + "images/testimonials/"
    this.data = data.testimonials.widget && data.testimonials.widget.length > 0 ? data.testimonials.widget : [];
  }
  componentDidMount() {
    TweenMax.to(this.container, 0.5, {autoAlpha: 1});
    TweenMax.set(this.AnimationElements, {autoAlpha: 0});
    this.animateIn();
  }
  componentWillUnmount() {
    TweenMax.set(this.container, {autoAlpha: 0});
  }
  setAnalyticSubmission = (name) => {
    if (name) {
      this.setState({analyticsSubmitted: {...this.state.analyticsSubmitted, [name]: true}})
    }
  }
  animateIn = () => {
    var tl = new TimelineMax({paused: true});
    tl.staggerFromTo(this.AnimationElements, 2, {autoAlpha: 0, y: 50}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 0.25);
    tl.play();
  }
  getTestimonial = (index) => {
    if (!this.animating) {
      this.animating = true;
      let nextIndex = this.state.current;
      if (index > 0) {
        nextIndex = this.state.current < this.data.length - 1 ? nextIndex + 1 : 0;
      } else if (index < 0) {
        nextIndex = this.state.current > 0 ? nextIndex - 1 : this.data.length - 1;
      }

      const interval = 0.5, direction = index > 0 ? -1 : 1;

      // Animate out
      TweenMax.staggerTo(this.TestimonialData.children, interval, { y: -10 * direction, autoAlpha: 0, ease: Power3.easeInOut, onComplete: () => {
        this.setState({ ...this.state, current: nextIndex });
        // Animate in
        TweenMax.staggerFromTo(this.TestimonialData.children, interval, { y: 10 * direction }, { y: 0, autoAlpha: 1, ease: Power3.easeInOut }, 0.1);
        this.animating = false;
      }}, 0.1);
    }
  }

  render() {
    this.AnimationElements = []
    const testimonial = this.data[this.state.current];
    
    return <div
      className="section-testimonials"
      // style={{backgroundImage: `url(${this.ASSET_PATH + 'background.jpg'})`}}
      ref={el => this.container = el}
    >
      { testimonial &&
        <Fragment>
          <div className="row" style={{ width: "100%" }}>
            <div className="col-10 offset-1 testimonials-heading">
              <h1>What Our Clients Are Saying</h1>
            </div>
          </div>
          <div className="row testimonial">
            <div className="col-10 offset-1 col-md-6 offset-md-3 testimonial-data-container" ref={el => this.TestimonialData = el}>
              <h4>{ testimonial.name }</h4>
              <p className="mini" style={{fontStyle: "italic"}}>{ testimonial.relationship }</p>
              <p>"{ testimonial.review }"</p>
            </div>
            <img className="chevron left" src={ChevronCircleSVG} alt="chevron-left"
               onClick={ this.getTestimonial.bind(this, -1) } style={{transform: "rotateY(-180deg)"}}/>
            <img className="chevron right" src={ChevronCircleSVG} alt="chevron-right"
                onClick={ this.getTestimonial.bind(this, 1) }/>
          </div>
        </Fragment>
      }
    </div>;
  }
};

export default Testimonials;
